@import "https://fonts.googleapis.com/css2?family=Pacifico&display=swap";
:root {
  --color-primary: #f38e82;
  --color-grey-light-1: #f9f5f3;
  --color-grey-light-2: #f2efee;
  --color-grey-light-3: #d3c7c3;
  --color-grey-dark-1: #615551;
  --color-grey-dark-2: #918581;
  --gradient: linear-gradient(to right bottom, #fbdb89, #f48982);
}

* {
  box-sizing: border-box;
  -o-sizing: border-box;
  -moz-sizing: border-box;
  -ms-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

@media only screen and (max-width: 980px) {
  html {
    font-size: 50%;
  }
}

body {
  color: var(--color-grey-dark-1);
  background-image: var(--gradient);
  min-height: calc(100vh - 8vw);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Nunito Sans, sans-serif;
  font-weight: 400;
  line-height: 1.6;
}

.container {
  max-width: 120rem;
  min-height: 117rem;
  background-color: #fff;
  border-radius: 9px;
  grid-template: "head head" 10rem
