  justify-content: space-between;
  padding: 4rem;
  display: flex;
}

.error__message--icon {
  color: var(--color-primary);
  font-size: 3.5rem;
}

.error__message p {
  text-align: center;
  margin-top: 1rem;
  padding: 0 1rem;
  font-size: 1.8rem;
}

.pagination {
