.btn--prev, .btn--next {
  color: var(--color-primary);
  text-transform: capitalize;
  background-color: var(--color-grey-light-1);
  border-radius: 2rem;
  justify-content: center;
  align-items: center;
  padding: .8rem 1.8rem;
  font-weight: bold;
  display: flex;
  position: absolute;
}

.btn--prev:hover, .btn--next:hover {
