.btn--next {
  right: 3.25rem;
}

.btn--prev .prev-icon {
  margin-right: .5rem;
}

.btn--next .next-icon {
  margin-left: .5rem;
}

.copyright {
  text-align: center;
  color: var(--color-grey-dark-2);
  margin-top: 3rem;
  padding: 0 3.25rem;
  font-size: 1.2rem;
}

.github-link {
  color: var(--color-grey-dark-2);
  font-size: 1.4rem;
}

.recipe {
  background-color: var(--color-grey-light-1);
  grid-area: recipe;
}

.recipe__message {
  text-align: center;
  padding: 4rem;
}

.recipe__message--icons {
