  100% {
    transform: rotate(360deg);
  }
}

.recipe__fig {
  height: 32rem;
  position: relative;
}

.recipe__fig:before {
  content: " ";
  width: 100%;
  height: 100%;
  opacity: .4;
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  position: absolute;
  top: 0;
